*, :before, :after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

:root {
  --primary-green: #a1bd90;
  --primary-yellow: #d8ac48;
  --light-grey: #e9e9e9;
  --olive-black: #101706;
  --bg-yellow: #d8ac4859;
  --menu-font-size: 1.75rem;
  --title-font-size: 2.5rem;
  --title-font-size-smaller: 2.2rem;
  --medium-font-weight: 400;
  --shadow-item: 4px 4px 19px -3px #353a30;
}

body {
  color: var(--olive-black);
  -webkit-font-smoothing: antialiased;
  font-family: Libre Bodoni, serif;
  line-height: 1.5;
}

picture, video, canvas, svg {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

p {
  padding-bottom: 2rem;
  font-size: 1.2rem;
}

ol, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
  list-style: none;
}

#root, #__next {
  isolation: isolate;
}

.header {
  background-color: var(--primary-green);
  flex-direction: column;
  height: 100vh;
  display: flex;
  position: relative;
}

.header__group {
  justify-content: space-between;
  min-height: 4.5rem;
  display: flex;
}

.header__btn__menu {
  background-color: #0000;
  border: none;
  height: 3.2rem;
  margin: auto 1rem auto 0;
  padding: 0;
}

.header__logo {
  max-width: 280px;
  padding: .9rem;
}

.header__nav {
  z-index: 10;
  opacity: 0;
  will-change: opacity, transform;
  pointer-events: none;
  background-color: #a1bd90;
  padding-top: 2.2rem;
  transition: opacity .8s, transform .8s;
  position: absolute;
  top: 5.2rem;
  left: 0;
  right: 0;
  transform: translateY(-12px);
}

.header__nav.active {
  opacity: 1;
  text-align: center;
  font-size: var(--menu-font-size);
  font-weight: var(--medium-font-weight);
  pointer-events: auto;
  transform: translateY(0);
}

.header__nav > ul {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.header__nav:not(.active) .nav__link {
  transition: none;
}

.nav__link {
  margin-bottom: 1.2em;
  font-size: 1.4rem;
  transition: scale .6s;
}

.nav__link:hover, .nav__link:focus {
  scale: 1.125;
}

.nav__link.btn:hover {
  scale: 1;
}

.nav__btn {
  letter-spacing: 1.2px;
  background-color: var(--primary-yellow);
  border-radius: 8px;
  margin-bottom: 2.4rem;
  padding: .6rem 2.6rem;
  font-family: Libre Caslon Text, serif;
  font-size: 1rem;
}

.nav__btn:hover {
  box-shadow: 5px 5px 9px -2px #353a30;
}

.header__img__container {
  width: 100%;
  height: 100%;
}

.header__img {
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 100%;
}

html {
  scroll-behavior: smooth;
}

.section {
  background-color: var(--bg-yellow);
  width: 100%;
  padding: 1.5%;
}

.section__container {
  flex-direction: column;
  margin: 0 auto;
  padding: 2rem 1rem;
  display: flex;
}

.section--bg-grey {
  background-color: var(--light-grey);
}

.section--bg-green {
  background-color: var(--primary-green);
}

.section__title {
  font-size: var(--title-font-size);
  font-weight: var(--medium-font-weight);
  padding-bottom: 1.6rem;
}

.section__title--smaller {
  font-size: var(--title-font-size-smaller);
}

.about-us {
  background-image: url("olive-branch.99fe5275.svg") no-repeat;
}

.section__contact__email {
  font-size: 8vw;
  font-style: italic;
}

.section_contact_img {
  z-index: 0;
  height: 75vw;
  position: absolute;
  left: 20vw;
}

.section__contact__container {
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.section__contact__title {
  font-size: 6vw;
}

.section__img {
  box-shadow: var(--shadow-item);
  border-radius: 8px;
  flex-shrink: 0;
  margin: 0 -1rem;
}

.section__img--no-padding {
  padding: 0;
}

.section__location__img {
  height: 70vh;
}

.img__about-us {
  margin-bottom: 2rem;
}

.footer__ul {
  grid-template-columns: 1fr 1fr;
  place-items: center;
  display: grid;
}

.nav__link--footer {
  font-size: 1.2rem;
}

.nav__btn--footer {
  width: 100%;
  margin: 2rem auto;
}

.footer__logo {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.footer__section__img {
  opacity: .76;
  max-height: 32vh;
}

.footer__address {
  text-align: center;
  margin-bottom: 2rem;
  padding: 2rem 0 0;
}

.section__subscribe__container {
  box-shadow: var(--shadow-item);
  background-color: #fdeabd;
  border-radius: 18px;
  gap: 2rem;
  margin: 2rem;
}

.subscribe-form {
  gap: .5rem;
  display: grid;
}

.subscribe-form .input-container > input {
  width: 100%;
}

.subscribe-form .input-container > label {
  margin-bottom: .2rem;
}

.subscribe-form .input-container > input {
  padding: .35rem .5rem;
  font-size: .9rem;
}

.subscribe-form .input-container > label {
  font-size: .9rem;
}

.subscribe-form .subscribe__btn {
  margin-top: 1rem;
}

input, select, textarea {
  border: 1px solid var(--olive-black);
  background-color: #fff;
  border-radius: 4px;
  width: 100%;
  padding: .6rem .75rem;
}

input:focus, select:focus, textarea:focus {
  border-color: var(--primary-yellow);
  outline: none;
  box-shadow: 0 0 0 2px #d8ac4833;
}

.booking-form .input-container > input, .booking-form .input-container > select, .booking-form .input-container > textarea {
  width: 100%;
}

.section__img--dining {
  margin-bottom: 2rem;
}

.header__nav .book-table__nav {
  flex-direction: row;
}

.booking-form {
  flex-direction: column;
  max-width: 500px;
  margin: auto;
  display: flex;
}

.booking-form button[type="submit"], .booking-form .submit-btn {
  margin-top: 1.5rem;
}

.input-container {
  min-width: 0;
  display: grid;
}

.booking-form .input-container {
  margin-bottom: 1rem;
}

.section__form {
  background-image: url("olive-branch.99fe5275.svg");
  background-size: cover;
  align-items: end;
  height: 100%;
  display: flex;
}

.header__form {
  padding: 1rem;
}

.form__olive-branch {
  z-index: -1;
  height: 100%;
  position: absolute;
  top: -1rem;
  left: -1rem;
}

.foot-note {
  text-align: center;
  margin: 0 auto;
  padding: 0;
  font-size: .7rem;
}

.chef-container {
  z-index: 0;
  background: url("knife.4bbf2798.webp") 10% 0 / contain no-repeat content-box border-box;
  position: relative;
  overflow: visible;
}

.section_container_facilities {
  background-image: url("olive.faff0901.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-origin: content-box;
}

.about-us {
  background-image: url("olive-branch.99fe5275.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-origin: content-box;
}

.buttonandthanklyou {
  text-align: justify;
  max-width: 360px;
}

.foot-thank {
  font-size: 1.1rem;
}

.form__container-2 {
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  display: grid;
}

.form__container-2 .input-container {
  margin: 0;
}

.booking-form .input-container > label, .form__container-2 .input-container > label {
  margin-bottom: .5rem;
}

.form__container-2 .input-container > input, .form__container-2 .input-container > select, .form__container-2 .input-container > datalist, .form__container-2 .input-container > input[list] {
  width: 100%;
  min-width: 0;
}

.booking-form > div:first-of-type {
  border-bottom: 1px solid var(--olive-black);
  margin-bottom: 1.25rem;
  padding-bottom: 1rem;
}

input {
  height: 2.1em;
}

nav__btn.submit-btn {
  margin-top: 2rem;
}

@media (width >= 600px) {
  .subscribe__text {
    max-width: 52%;
  }

  .booking-form button[type="submit"], .booking-form .submit-btn {
    margin-top: 2rem;
  }

  .section {
    padding: 3%;
  }

  .section__subscribe__container {
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  .subscribe__btn {
    text-align: center;
    min-width: 35%;
    margin: 0;
  }

  .section__img--dining {
    padding-bottom: 0;
  }

  .header__group {
    height: auto;
  }

  .section__container--footer {
    flex-direction: row;
    justify-content: space-between;
    align-items: stretch;
    gap: 3rem;
  }

  .footer__logo {
    flex-direction: row;
    height: 200px;
  }

  .footer__address {
    padding: 0;
  }

  .footer__btn__link {
    justify-content: center;
    margin-top: auto;
    display: flex;
  }

  .section_contact_img {
    height: 70vh;
  }

  .footer__section__img {
    height: 100%;
  }

  .footer__address {
    text-align: start;
    align-self: end;
    width: 120px;
    margin: 0 0 0 .4rem;
    font-size: 1rem;
  }

  .nav__btn--footer {
    align-self: center;
    max-width: 300px;
    margin: 0 auto;
  }

  .footer__ul {
    flex-direction: column;
    justify-content: space-between;
    place-items: end;
    height: 100%;
    display: flex;
  }

  .chef-container {
    background-position: top;
    background-size: 35%;
  }

  .nav__link--footer {
    margin: 0;
    padding: 0;
  }

  .buttonandthanklyou {
    flex-direction: column;
    display: flex;
  }

  .foot-note {
    letter-spacing: 3px;
  }
}

@media (width >= 1000px) {
  .subscribe-form {
    width: clamp(240px, 60%, 420px);
  }

  .chef-container {
    background-position: 0;
    background-size: 40%;
  }

  .section__container {
    max-width: 1440px;
  }

  .header__logo {
    max-width: 340px;
  }

  .section__container {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 4rem;
    padding: 6rem 4rem;
    font-size: 1.6rem;
  }

  .section__container--footer {
    align-items: stretch;
  }

  .section__img {
    width: 45%;
    margin: 0;
  }

  .section__location__img {
    width: 60%;
  }

  .section_contact_img {
    height: 70vh;
    left: 30vw;
  }

  .section__contact__container {
    justify-content: center;
  }

  .section__subscribe__container {
    width: clamp(800px, 60%, 1100px);
    margin: 4rem auto;
  }

  .section_container_facilities {
    grid-template-columns: 1fr 1fr;
    display: grid;
  }

  .section__img--dining {
    width: 100%;
  }

  .header__nav {
    text-align: center;
    width: auto;
    height: 100%;
    font-size: var(--menu-font-size);
    z-index: auto;
    opacity: 1;
    pointer-events: auto;
    background: none;
    grid-area: navi;
    justify-content: center;
    align-items: start;
    padding-top: 0;
    display: flex;
    position: static;
    transform: none;
  }

  .header__img__container {
    grid-area: img;
  }

  .header {
    grid-template: "logo img"
                   "navi img"
                   / 2fr 7fr;
    display: grid;
    overflow: hidden;
  }

  .header__logo {
    justify-self: center;
    margin: 4rem auto;
  }

  .header__btn__menu {
    display: none;
  }

  .section__contact__title {
    font-size: 2.5vw;
  }

  .section__contact__email {
    font-size: 5vw;
  }

  .form__olive-branch {
    z-index: -1;
    width: 100%;
    height: auto;
    position: absolute;
    top: 4rem;
    left: 0;
    scale: 120%;
  }

  .booking-form button[type="submit"], .booking-form .submit-btn {
    margin-top: 3rem;
  }
}
